@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  line-height: 1.1;
  font-weight: 600;
  color: #ffffff;
  background-color: #5f458c;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -100;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}
@keyframes fadeInRight {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes zoomIn {
  to {
    transform: scaleX(1);
    opacity: 1;
  }
}
@keyframes zoomInPulsed {
  to {
    transform: scale(1, 1);
    opacity: 1;
  }
}
@keyframes showing {
  0% {
    transform: translate3d(0, 100%, 0);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes zoomInPulsedLarge {
  0% {
    transform: translate3d(0, 100%, 0) scale3d(0, 0, 0);
    opacity: 0;
  }
  50% {
    transform: translate3d(0, 0, 0) scale3d(1.1, 1.1, 1.1);
  }
  100% {
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    opacity: 1;
  }
}
@keyframes shakeY {
  0% {
    transform: translate3d(0, 100%, 0) rotate3d(0, 0, 0, 0);
    opacity: 0;
  }
  10% {
    transform: translate3d(0, -5%, 0) rotate3d(1, 1, 1, -3deg);
  }
  30% {
    transform: translate3d(0, 5%, 0) rotate3d(1, 1, 1, 3deg);
    opacity: 1;
  }
  50% {
    transform: translate3d(0, -5%, 0) rotate3d(1, 1, 1, -3deg);
  }
  80% {
    transform: translate3d(0, 5%, 0) rotate3d(1, 1, 1, 3deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate3d(0, 0, 0, 0);
  }
}
@keyframes slideLeft {
  0% {
    transform: scaleX(0);
    transform-origin: 100% 0;
  }
  100% {
    opacity: 1;
    transform: scaleX(1);
    transform-origin: 100% 100%;
  }
}
