  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;
  transform: translate3d(0, 1rem, 0);
}
@media (min-width: 2000px) {
  .slider__item-title {
    margin-left: -55px;
    font-size: 48px;
  }
}
@media (orientation: portrait) {
  .slider__item-title {
    margin-left: -2.5rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .slider__item-title {
    margin-left: -2.5rem;
    font-size: 3rem;
  }
}

.slider__item-text {
  margin-top: 3.6rem;
  margin-bottom: 0;
  max-width: 34rem;
  font-size: 1.8rem;
  line-height: 2rem;
  transform: translateX(20px);
}
@media (min-width: 2000px) {
  .slider__item-text {
    margin-top: 36px;
    max-width: 340px;
    font-size: 18px;
    line-height: 20px;
  }
}
@media (orientation: portrait) {
  .slider__item-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: auto;
    min-height: 11.6rem;
    font-size: 1.4rem;
    line-height: 1.5rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .slider__item-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: auto;
    min-height: 14rem;
    font-size: 1.4rem;
    line-height: 1.5rem;
  }
}

.slider__controls {
  position: absolute;
  bottom: 2.8rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  width: 17rem;
  z-index: 1;
}
@media (min-width: 2000px) {
  .slider__controls {
    bottom: 28px;
    width: 170px;
  }
}
@media (orientation: portrait) {
  .slider__controls {
    display: none;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .slider__controls {
    display: none;
  }
}

.slider__control {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  width: 4rem;
  height: 4rem;
  color: #5f458c;
}
.slider__control:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .slider__control {
    width: 40px;
    height: 40px;
  }
}
.slider__control::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 50%;
  z-index: -1;
}
.slider__control svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 1rem;
  height: 2rem;
  pointer-events: none;
}
@media (min-width: 2000px) {
  .slider__control svg {
    width: 10px;
    height: 20px;
  }
}
.slider__control:hover {
  color: #a67ee5;
}
.slider__control:hover::before {
  width: 112.5%;
  height: 112.5%;
}
.slider__control:focus {
  color: #a67ee5;
  outline: none;
}
.slider__control:focus::before {
  border-color: #a67ee5;
}
.slider__control:active, .slider__control:active:focus {
  color: #ffffff;
}
.slider__control:active::before, .slider__control:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}
.slider__control.swiper-button-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.slider__pagination.swiper-pagination {
  position: absolute;
  bottom: 2.8rem;
  left: 50%;
  transform: translateX(-50%);
  width: 9rem;
  height: 4rem;
  text-align: center;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 4rem;
}
@media (min-width: 2000px) {
  .slider__pagination.swiper-pagination {
    bottom: 28px;
    width: 90px;
    height: 40px;
    font-size: 24px;
    line-height: 40px;
  }
}
@media (orientation: portrait) {
  .slider__pagination.swiper-pagination {
    width: 100%;
    left: 0;
    right: 0;
    bottom: 2.1rem;
    transform: none;
    margin: 0;
    height: 2rem;
  }
  .slider__pagination.swiper-pagination .swiper-pagination-bullet {
    margin: 0 0.6rem;
    width: 0.4rem;
    height: 0.4rem;
    background-color: #ffffff;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .slider__pagination.swiper-pagination {
    width: 100%;
    left: 0;
    right: 0;
    bottom: 2.1rem;
    transform: none;
    margin: 0;
    height: 2rem;
  }
  .slider__pagination.swiper-pagination .swiper-pagination-bullet {
    margin: 0 0.6rem;
    width: 0.4rem;
    height: 0.4rem;
    background-color: #ffffff;
  }
}

.social-block {
  position: relative;
}
.social-block .social-block__link--fb,
.social-block .social-block__link--insta,
.social-block .social-block__link--vk {
  opacity: 0;
}
.social-block:hover .social-block__list {
  pointer-events: auto;
  transition-property: opacity;
}
.social-block:hover .social-block__list .social-block__link--fb {
  transition-property: opacity;
  transition-duration: 200ms;
  opacity: 1;
  transition-delay: 300ms;
  transition-timing-function: cubic-bezier(0.5, -0.3, 0.6, 0.9);
}
.social-block:hover .social-block__list .social-block__link--insta {
  transition-property: opacity;
  transition-duration: 200ms;
  opacity: 1;
  transition-delay: 500ms;
  transition-timing-function: cubic-bezier(0.5, -0.3, 0.6, 0.9);
}
.social-block:hover .social-block__list .social-block__link--vk {
  transition-property: opacity;
  transition-duration: 200ms;
  opacity: 1;
  transition-delay: 700ms;
  transition-timing-function: cubic-bezier(0.5, -0.3, 0.6, 0.9);
}

.page-loaded .social-block__toggler:before {
  width: 100%;
  height: 100%;
}

.social-block__toggler {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: #5f458c;
  border-radius: 50%;
}
.social-block__toggler:focus {
  outline: none;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__toggler {
    display: none;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__toggler {
    display: none;
  }
}
.social-block__toggler::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  z-index: -1;
  width: 0;
  height: 0;
  transition-property: width, height;
  transition-timing-function: cubic-bezier(1, 1.5, 1, 1);
  transition-duration: 500ms;
}
.social-block__toggler svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 2.3rem;
  height: 2.2rem;
}
.social-block__toggler:hover, .social-block--active .social-block__toggler {
  color: #a67ee5;
  opacity: 1;
  transition: color 300ms ease;
}
.social-block__toggler:hover::before, .social-block--active .social-block__toggler::before {
  transition-property: width, height;
  transition-duration: 300ms;
  transition-timing-function: ease;
  width: 112.5%;
  height: 112.5%;
}
.social-block__toggler:focus {
  color: #a67ee5;
  outline: none;
}
.social-block__toggler:focus::before {
  border-color: #a67ee5;
}
.social-block__toggler:focus + .social-block__list {
  opacity: 1;
  pointer-events: auto;
}
.social-block__toggler:active, .social-block__toggler:active:focus {
  color: #ffffff;
}
.social-block__toggler:active::before, .social-block__toggler:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.social-block__list {
  padding: 0;
  margin: 0;
  list-style: none;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 2000px) {
  .social-block__list {
    margin-top: 15px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__list {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__list {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }
}
.social-block__list::before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  height: 1.5rem;
}
@media (min-width: 2000px) {
  .social-block__list::before {
    height: 15px;
  }
}
.social-block__list:hover {
  opacity: 1;
  pointer-events: auto;
}
.social-block__list:focus-within {
  opacity: 1;
  pointer-events: auto;
}
.social-block--active .social-block__list {
  opacity: 1;
  pointer-events: auto;
}
.social-block__list li {
  flex: none;
  margin-bottom: 0.5rem;
}
@media (min-width: 2000px) {
  .social-block__list li {
    margin-bottom: 5px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: #ffffff;
  border: 2px solid transparent;
  border-radius: 50%;
}
@media (min-width: 2000px) {
  .social-block__link {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__link {
    color: #ffffff;
    background-color: #5f458c;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__link {
    color: #ffffff;
    background-color: #5f458c;
  }
}
.social-block__link:hover {
  color: #a67ee5;
}
.social-block__link:focus {
  color: #a67ee5;
  background-color: #ffffff;
  border-color: #a67ee5;
  outline: none;
}
.social-block__link:active, .social-block__link:active:focus {
  color: #a67ee5;
  background-color: #ffffff;
  border-color: #ffffff;
}
.social-block__link svg {
